
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 100%;
	vertical-align: baseline;
  text-decoration: none;
}

ul {
  list-style: none;
}

table {
  border-collapse: collapse;
}

input, textarea, button{
  outline: none;
}

th {
  text-align: inherit;
}
